//注册axios
Vue.use(VueAxios, axios)

//配置请求基础路径
axios.defaults.baseURL = 'http://www.kangliuyong.com:10002'

//配置post请求头
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';

//发起请求之前处理
axios.interceptors.request.use(config => {
  // 

  if (config.method == 'post') {
    let paramsString = '';
    //post请求参数序列化，转成一个字符串
    for (let key in config.data) {
      paramsString += key + '=' + config.data[key] + '&'
    }

    // 

    config.data = paramsString.slice(0, -1);

    // 
  }

  return config;
})

//添加appkey属性保存appkey
Vue.prototype.appkey = 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=';


//全局过滤器
Vue.filter('formatDate',(value,format) => {
  // 
  let currentDate = new Date(value)

  let year = currentDate.getFullYear()
  // 
  
  if(/(y+)/.test(format)){
    //获取匹配组的内容
    let yearContent = RegExp.$1;
    // 
    format = format.replace(yearContent,year)
  }

  //处理月日时分秒
  let dateObject = {
    M: currentDate.getMonth() + 1,
    D: currentDate.getDate(),
    h: currentDate.getHours(),
    m: currentDate.getMinutes(),
    s: currentDate.getSeconds()
  };

  for (let key in dateObject) {
    //创建动态正则表达式
    let reg = new RegExp(`(${key}+)`)
    // 
    if (reg.test(format)) {
      //获取匹配组的内容, RegExp.$n必须需要test验证通过可以获取
      let content = RegExp.$1;
      // 

      //控制补零
      let value = dateObject[key] >= 10 ? dateObject[key] : content.length == 2 ? '0' + dateObject[key] : dateObject[key];
      format = format.replace(content, value);
    }
  }
  return format
})

Vue.config.productionTip = false



new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
