export const mutations = {
    //将swiper数据存入到state缓存中
    changeData(state,o){
        state[o.key] = o.value
    },
    //修改商品数据
  changeProducts(state, o) {
    //data: 载荷
    //data.type: 类型
    //data.value: 商品数据
    state.ProductData_ByTypeCache[o.type] = o.value;
  },

  //修改当前下标
  changeCurrent(state,o){
    state.current = o.current
    state.tops = o.top
    state.currentDataObjs = o.currentDataObj
  }
}